html, body {
  margin: 0;
  padding: 0;
  font-family: ‘Open Sans’, sans-serif;
  font-size: 18px;
}

div {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 12px;
}

h1 {
  color: #111;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 64px;
  font-weight: 700;
  line-height: 64px;
  margin: 0 0 0;
  padding: 20px 30px;
  text-align: center;
  text-transform: uppercase;
}

h2 {
  color: #111;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  margin: 0 0 24px;
  padding: 0 30px;
  text-align: center;
  text-transform: uppercase;
}

h3 {
  color: #111;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 36px;
  margin: 0 0 24px;
  padding: 0 30px;
  text-align: center;
  /*text-transform: uppercase;*/
}

h4 {
  color: #111;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  margin: 0 0 12px;
  padding: 0 10px;
  text-align: center;
  /*text-transform: uppercase;*/
}

h5 {
  color: #111;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 22px;
  margin: 0 0 6px;
  padding: 0 6px;
  text-align: center;
}

h6 {
  color: #111;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  margin: 0 0 6px;
  padding: 0 6px;
  text-align: center;
}

p {
  color: #111;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 26px;
  margin: 0 0 26px;
}

a {
  color: #dc0400;
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  text-decoration: underline
}

button {
  border: none;
  outline: none;
  background: none;

  cursor: pointer;
}

@media screen and (max-width: 768px) {
  p {
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 22px;
  }
  h1 {
    font-size: 48px;
    font-weight: 700;
    line-height: 48px;
    margin: 0 0 0;
    padding: 20px 20px;
  }
  h2 {
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    margin: 0 0 0;
    padding: 20px 20px;
  }
  li {
    font-size: 16px;
  }
}


